@charset "utf-8";


body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 1em;
}
a {
	text-decoration: none;
	color: #000;
}
ul, ol {
	list-style: none;
}
img {
	max-width: 100%;
	vertical-align: top;
	border: none;
}
body {
	font-family: "Open Sans", "Noto Sans KR","open sans" ,sans-serif;
}
body.dark {
	background-color: #000;
}
.dark #sec2 {
	background-color: #fff;
}
.dark #sec4 {
	background-color: #fff;
}

#main {
	margin: 0 auto;
	width: 100%;
	height: 100vh;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #000;
	overflow: hidden;
}

#main header {
    display: flex;
    height: 50px;
    justify-content: space-between;
	align-items: center;
    position: fixed;
    width: 100%;
    z-index: 100;
	top: 0;
	background-color: #000;
}
#main header .logo a {	
	display: block;
	font-size: 1.5rem;
	font-weight: 900;
	padding:  0 5vw;
	font-family: "Open Sans";
	color: #fff;
}
#gnb {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 15px 20px;
	padding-right: 5vw;
}
#gnb ul {
	display: flex;
	gap: 30px;
}
#gnb ul li a {
	font-size: 0.913em;
	color: #fff;
	font-family:"Oswald";
	font-weight: 400;
}
#gnb li.active a {
	color: #C3DC27;
}
#gnb.fixed li.active a {
	color: #fff;
}
#gnb ul li:hover a {
	color: #C3DC27;
	transition:  0.5s;
	border-bottom: 1px solid #C3DC27;
}
#main header .tab {
	display: none;
	position: fixed;
	right: 30px;
	top: 20px;
	z-index: 1001;
	width: 30px;
	height: 23px;
	cursor: pointer;
}
header .tab span {
    width: 100%;
    height: 3px;
    background-color: #fff;
}
header .tab.active span {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
	background-color: #fff;
}
header .tab.active span:nth-child(1) {
transform: rotate(45deg);
}
header .tab.active span:nth-child(2) {
display: none;
}
header .tab.active span:nth-child(3) {
transform: rotate(-45deg);
}
header .dim {
	display: none;
	/* display: block; */
	position: fixed;
	left: 0;
	top: 0;
	width: 100vw;
	height: 100vh;
	background-color: #000;
}
header .dim.active {
	display: block;
}
#mobile {
	display: none;
	/* display: block; */
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1000;
	width: 100%;
	height: 100vh;
	padding: 30px 20px;
	transition: right 0.3s;
	box-sizing: border-box;
}
#mobile.active {
	display: block;	
}
#mobile ul {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

#mobile a {
	display: block;
	padding: 20px 0;
	font-size: 2em;
	font-weight: 700;
	color: #C3DC27;
}

#main .sidebar {
	width: 30px;
	position: fixed;
	left: 0; 
	top: 50px;
	height: calc(100vh - 50px);
	z-index: 10;
}
#main .sidebar ul li {
	background-color: #fff;
}
#main .sidebar ul li.active {
	background-color: #c3dc27;
}
#main .sidebar ul li a {
	writing-mode: vertical-lr;
	line-height: 34px;
	height: 35px;
	padding: 10px 0;
	font-size: 0.75em;
	font-weight: 400;
	letter-spacing: 1px;
	box-sizing: border-box;
	transform: rotate(180deg);
	transform-origin: center center;
}
#main .sidebar ul li.active a {
	text-align: right;
	font-weight: 700;
	height: calc(100vh - 205px);
}
#main .sidebar ul li a span.em {
	display: none;
}
#main .sidebar ul li.active a span.em {
	display: inline;
}
#main .visual {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	width: 100%;
	padding: 0 5vw;
}
#main .visual  .title {
	position: relative;
	margin: 25px 0;
	color: #fff;
	width: calc(100% - 250px);
	display: flex;
	flex-direction: column;
	overflow: hidden;
}
#main .visual  .title span.line {
	display: block;
    width: 120%;
    height: 1px;
    background: rgba(255, 255, 255, .32);
    animation: line 0.6s 2s linear forwards;
	animation-duration: 1s;
    position: absolute;
	bottom: 0;
	left: 0;
	animation-delay:0.5s;
}
/* #main .visual  .title .line::after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 1px;
	background-color: #fff;
	animation-delay:0.5s;
} */
#main .visual  .title span {
	font-size: 5vw;
	font-weight: 900;
	font-style: italic;
}
#main .visual  .title strong {
	font-size: 5vw;
	font-weight: 900;
	font-style: italic;
}
#main .keytxt {
	position: relative;
	bottom: -14px;
	color: #fff;
	font-size: 1em;
	line-height: 1.8;
	width: 250px;
	text-align: right;
	white-space: nowrap;
	overflow: hidden;
}
#main .track {
	position: absolute;
	top: 75vh;
	display: flex;
}
#main .track .track_txt {
	display: inline-block;
	font-size: 11vw;
	font-weight: 900;
	font-style: italic;
	color: transparent;
	white-space: nowrap;
	-webkit-text-stroke: 2px #80901A;
	line-height: 100%;
}

@media only screen and (max-width: 1380px) {
	#main .visual  .title .line::after {
		width: 100%; 
	}
	#main .track {
		bottom:80px;
	}
}


@media  only screen and (max-width:1280px){
	#main .track{ 
		bottom: 60px;
	}
}


@media only screen and (max-width:1024px){
	#main .track { 
		bottom: 40px; 
	}	
}

@media only screen and (max-width:940px){
	#main .sidebar {
		width: 100%;
		top: auto;
		bottom: 0;
		height: 35px;
		background-color: #fff
	}
	#main .sidebar ul {
		display: flex;
	}
	#main .sidebar ul li a {
		display: block;
		padding: 0 10px;
		width: 35px;
		height: auto;
		text-align: center;
		writing-mode: horizontal-tb;
		transform: rotate(0deg);
	}
	#main .sidebar ul li.active a {
		width: calc(100vw - 160px);
	}
}
