@media all and (max-width:720px){
	#main header .logo a {
		padding-left: 40px;
	}
	#gnb {
		display: none;
	}
	#mobile .inner ul {
		flex-direction: column;
	}
	#mobile .inner ul li {
		margin-left: 0;
		margin-bottom: 45px;
		text-align: center;
	}
	#mobile .inner ul li:last-child {
		margin-bottom: 0;
	}
	#mobile .inner ul li a {
		font-size: 1.2em;
