		font-size: 2.25em;
	}
	#main .visual  .title .line::after {
		width:100%; 
	}
	#main .keytxt strong {
		padding:10px 0 0;
	}
	#main .track{
		bottom:30px;
	}
}
@media all and (max-width:640px){
	#main .keytxt {
		display: none;
	}
	#main .visual  .title .line::after {
